import React from 'react';
import PlayerMenu from '../../PlayerMenu';
import VerticalView from './VerticalView';
import './VerticalViewOverlay.css';

const VerticalViewOverlay = ({
  isActive,
  blocks,
  trackNames,
  cellsState,
  currentPlaybackPosition,
  handleCellClick,
  currentBar,
  totalBars,
  onBarChange,
  currentBarInfo,
  sequencerStructure,
  onVerticalModeChange,
  isPlaying,
  bpm,
  isInitialized,
  isPrerollEnabled,
  isTransitioning,
  onPlayToggle,
  onBpmChange,
  onPrerollToggle,
  loopedBlockIndices,
  mutedTracks,
  handleMuteToggle
}) => {
  if (!isActive) return null;

  const handleClose = () => {
    if (onVerticalModeChange) {
      onVerticalModeChange('horizontal');
    }
  };

  return (
    <div className="vertical-view-overlay">
      <div className="vertical-mode-toggle">
        <button className="vertical-mode-close-btn" onClick={handleClose}>
          <span></span>
        </button>
      </div>

      <div className="vertical-view-content">
        <VerticalView
          blocks={blocks}
          trackNames={trackNames}
          cellsState={cellsState}
          currentPlaybackPosition={currentPlaybackPosition}
          handleCellClick={handleCellClick}
          loopedBlockIndices={loopedBlockIndices}
          mutedTracks={mutedTracks}
          handleMuteToggle={handleMuteToggle}
        />
      </div>
      
      <div className="vertical-view-player-menu">
        <PlayerMenu 
          sequencerStructure={sequencerStructure}
          trackNames={trackNames}
          cellsState={cellsState}
          currentBar={currentBar}
          totalBars={totalBars}
          onBarChange={onBarChange}
          currentBarInfo={currentBarInfo}
          onVerticalModeChange={onVerticalModeChange}
          inVerticalMode={true}
          isPlaying={isPlaying}
          bpm={bpm}
          isInitialized={isInitialized}
          isPrerollEnabled={isPrerollEnabled}
          isTransitioning={isTransitioning}
          onPlayToggle={onPlayToggle}
          onBpmChange={onBpmChange}
          onPrerollToggle={onPrerollToggle}
          loopedBlockIndices={loopedBlockIndices}
          mutedTracks={mutedTracks}
          handleMuteToggle={handleMuteToggle}
        />
      </div>
    </div>
  );
};

export default VerticalViewOverlay; 
 