import React, { useState, useEffect, useRef } from 'react';
import './VerticalView.css';

const VerticalView = ({ 
  blocks,
  trackNames,
  cellsState,
  currentPlaybackPosition,
  handleCellClick,
  loopedBlockIndices,
  mutedTracks,
  handleMuteToggle
}) => {
  //  ref      (  )
  const blocksSequence = useRef([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  
  //      
  const [topBlock, setTopBlock] = useState(null);
  const [bottomBlock, setBottomBlock] = useState(null);
  
  //     (top  bottom)
  const [activePosition, setActivePosition] = useState('top');

  // /     blocks  loopedBlockIndices
  useEffect(() => {
    let effectiveBlocksSequence = [];

    if (loopedBlockIndices && loopedBlockIndices.length > 0) {
      //     
      console.log("VerticalView: Building sequence from looped indices:", loopedBlockIndices);
      loopedBlockIndices.forEach((originalIndex, sequenceIdx) => {
        if (blocks && originalIndex >= 0 && originalIndex < blocks.length) {
          effectiveBlocksSequence.push({
            ...blocks[originalIndex],
            originalIndex: originalIndex, // ,  originalIndex 
            sequenceIndex: sequenceIdx //    
          });
        } else {
             console.warn(`VerticalView: Invalid originalIndex ${originalIndex} from loop list.`);
        }
      });
       console.log("VerticalView: Effective loop sequence:", effectiveBlocksSequence);
    } else if (blocks && blocks.length > 0) {
      //   
      console.log("VerticalView: Building full sequence.");
      effectiveBlocksSequence = blocks.map((block, index) => ({
        ...block,
        // :      originalIndex ( SequencerModule),  .
        // , ,      blocks   originalIndex.
        originalIndex: block.originalIndex !== undefined ? block.originalIndex : index,
        sequenceIndex: index //     
      }));
    }

    blocksSequence.current = effectiveBlocksSequence;

    //  / 
    if (effectiveBlocksSequence.length > 0) {
      setTopBlock(effectiveBlocksSequence[0]);
      setBottomBlock(effectiveBlocksSequence.length > 1 
        ? effectiveBlocksSequence[1] 
        : effectiveBlocksSequence[0] //      
      );
      setCurrentIndex(0); //      
       setActivePosition('top'); //     
    } else {
      setTopBlock(null);
      setBottomBlock(null);
    }
  }, [blocks, loopedBlockIndices]); //   blocks  loopedBlockIndices

  //        
  useEffect(() => {
    if (!currentPlaybackPosition || !blocksSequence.current.length) return;

    //  ,   ,       
    const playingBlockIndex = blocksSequence.current.findIndex(block => {
      // block.originalIndex      useEffect
      return block.originalIndex === currentPlaybackPosition.blockIndex;
    });

    if (playingBlockIndex === -1) return;
    
    //     ,  
    if (playingBlockIndex !== currentIndex) {
      setCurrentIndex(playingBlockIndex);
      
      // ,   (/)    
      const newPosition = (playingBlockIndex % 2 === 0) ? 'top' : 'bottom';
      setActivePosition(newPosition);
      
      //        
      if (newPosition === 'top') {
        setTopBlock(blocksSequence.current[playingBlockIndex]);
        
        //         
        const nextIndex = (playingBlockIndex + 1) % blocksSequence.current.length;
        setBottomBlock(blocksSequence.current[nextIndex]);
      } else {
        setBottomBlock(blocksSequence.current[playingBlockIndex]);
        
        //         
        const nextIndex = (playingBlockIndex + 1) % blocksSequence.current.length;
        setTopBlock(blocksSequence.current[nextIndex]);
      }
    }
  }, [currentPlaybackPosition, currentIndex]);

  //     
  const getCellState = (blockIndex, trackName, stepIndex) => {
    const cellId = `${blockIndex}-${trackName}-${stepIndex}`;
    return cellsState[cellId] || { instrumentId: null };
  };

  //      
  if (!topBlock || !bottomBlock) {
    return <div className="vertical-view-loading">...</div>;
  }

  //        
  const getMaxSteps = (block) => {
    return Math.max(...Object.values(block.trackSteps || {})) || 1;
  };

  //     ( )
  const getCurrentProgress = () => {
    if (!currentPlaybackPosition || !blocksSequence.current || blocksSequence.current.length === 0) return 0;
    
    //   ,   ,   
    const playingBlockIndex = blocksSequence.current.findIndex(block => {
      return block.originalIndex === currentPlaybackPosition.blockIndex;
    });
    
    //    ,  0
    if (playingBlockIndex === -1) return 0;
    
    //       
    let totalSteps = 0;
    let passedSteps = 0;
    
    //     
    blocksSequence.current.forEach((block, idx) => {
      const maxStepsInBlock = getMaxSteps(block);
      totalSteps += maxStepsInBlock;
      
      //     ,      
      if (idx < playingBlockIndex) {
        passedSteps += maxStepsInBlock;
      }
      //    ,    
      else if (idx === playingBlockIndex) {
        passedSteps += currentPlaybackPosition.stepIndex;
      }
    });
    
    //    
    if (totalSteps === 0) return 0;
    
    //       
    return (passedSteps / totalSteps) * 100;
  };

  // ,      
  const isBlockActive = (position) => {
    return position === activePosition;
  };

  //     
  const getTrackColor = (trackName) => {
    switch(trackName.toLowerCase()) {
      case 'lf': return '#3fc73f'; //     
      case 'rf': return '#f44336'; //    
      case 'l': return '#29b6f6';  //    
      case 'r': return '#ff9800';  //    
      default: return '#9c27b0';   //   
    }
  };

  //     : LF, RF, L, R
  const sortTracks = (tracks) => {
    const order = { 'lf': 0, 'rf': 1, 'l': 2, 'r': 3 };
    
    return [...tracks].sort((a, b) => {
      const aOrder = order[a.toLowerCase()] !== undefined ? order[a.toLowerCase()] : 999;
      const bOrder = order[b.toLowerCase()] !== undefined ? order[b.toLowerCase()] : 999;
      return aOrder - bOrder;
    });
  };

  return (
    <div className="vertical-view-container">
      {/*   */}
      {currentPlaybackPosition && (
        <div className="vertical-progress-bar">
          <div 
            className="vertical-progress-indicator" 
            style={{ width: `${getCurrentProgress()}%` }} 
          />
        </div>
      )}
      
      {/*       */}
      <div className="vertical-header">
        {sortTracks(trackNames).map(trackName => (
          <div 
            key={`header-${trackName}`} 
            className={`vertical-track-label ${mutedTracks instanceof Set && mutedTracks.has(trackName) ? 'muted' : ''}`}
            style={{ backgroundColor: mutedTracks instanceof Set && mutedTracks.has(trackName) ? 'transparent' : getTrackColor(trackName) }}
            onClick={() => {
              if (typeof handleMuteToggle === 'function') {
                handleMuteToggle(trackName);
              } else {
                console.error('VerticalView Error: handleMuteToggle is not a function when clicking label!', handleMuteToggle);
              }
            }}
          >
            {trackName.toUpperCase()}
          </div>
        ))}
      </div>

      {/*   */}
      <div className="vertical-blocks-container">
        {/*   */}
        <div className={`vertical-block ${isBlockActive('top') ? 'active-block' : 'inactive-block'}`}>
          <div className="block-number">{topBlock.sequenceIndex + 1}</div>
          <div className="vertical-grid">
            {sortTracks(trackNames).map(trackName => (
              <div 
                key={`top-${trackName}`} 
                className={`vertical-track-column ${mutedTracks instanceof Set && mutedTracks.has(trackName) ? 'muted' : ''}`}
              >
                {Array.from({ length: topBlock.trackSteps[trackName] }, (_, stepIndex) => {
                  const originalBlockIndex = topBlock.originalIndex !== undefined 
                    ? topBlock.originalIndex 
                    : topBlock.sequenceIndex; // Fallback,  originalIndex  
                  
                  const cellId = `${originalBlockIndex}-${trackName}-${stepIndex}`;
                  const cellState = getCellState(originalBlockIndex, trackName, stepIndex);
                  const isActive = !!cellState.instrumentId;
                  
                  // ,         
                  const isCurrentlyPlaying = currentPlaybackPosition && 
                    isBlockActive('top') &&
                    currentPlaybackPosition.stepIndex === stepIndex &&
                    !(mutedTracks instanceof Set && mutedTracks.has(trackName));
                  
                  let playingClass = '';
                  if (isCurrentlyPlaying) {
                    playingClass = isActive ? 'playing-active' : 'playing-empty';
                  }

                  return (
                    <div
                      key={`top-${cellId}`}
                      className={`vertical-grid-cell ${isActive ? 'active' : ''} ${playingClass}`}
                      onClick={() => handleCellClick(cellId)}
                      data-cell-id={cellId}
                    >
                      {isActive && (
                        <div 
                          className="cell-instrument-marker"
                          style={{ backgroundColor: getTrackColor(trackName) }}
                        />
                      )}
                    </div>
                  );
                })}
              </div>
            ))}
          </div>
        </div>

        {/*   */}
        <div className={`vertical-block ${isBlockActive('bottom') ? 'active-block' : 'inactive-block'}`}>
          <div className="block-number">{bottomBlock.sequenceIndex + 1}</div>
          <div className="vertical-grid">
            {sortTracks(trackNames).map(trackName => (
              <div 
                key={`bottom-${trackName}`} 
                className={`vertical-track-column ${mutedTracks instanceof Set && mutedTracks.has(trackName) ? 'muted' : ''}`}
              >
                {Array.from({ length: bottomBlock.trackSteps[trackName] }, (_, stepIndex) => {
                  const originalBlockIndex = bottomBlock.originalIndex !== undefined 
                    ? bottomBlock.originalIndex 
                    : bottomBlock.sequenceIndex; // Fallback,  originalIndex  
                  
                  const cellId = `${originalBlockIndex}-${trackName}-${stepIndex}`;
                  const cellState = getCellState(originalBlockIndex, trackName, stepIndex);
                  const isActive = !!cellState.instrumentId;
                  
                  // ,         
                  const isCurrentlyPlaying = currentPlaybackPosition && 
                    isBlockActive('bottom') &&
                    currentPlaybackPosition.stepIndex === stepIndex &&
                    !(mutedTracks instanceof Set && mutedTracks.has(trackName));
                  
                  let playingClass = '';
                  if (isCurrentlyPlaying) {
                    playingClass = isActive ? 'playing-active' : 'playing-empty';
                  }

                  return (
                    <div
                      key={`bottom-${cellId}`}
                      className={`vertical-grid-cell ${isActive ? 'active' : ''} ${playingClass}`}
                      onClick={() => handleCellClick(cellId)}
                      data-cell-id={cellId}
                    >
                      {isActive && (
                        <div 
                          className="cell-instrument-marker"
                          style={{ backgroundColor: getTrackColor(trackName) }}
                        />
                      )}
                    </div>
                  );
                })}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default VerticalView; 