import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import './App.css';
import SequencerPage from './pages/SequencerPage';
import SavedSessionsPage from './pages/SavedSessionsPage';
import { viewport } from '@telegram-apps/sdk';

function App() {
  const [isFullscreen, setIsFullscreen] = useState(false);

  //    
  useEffect(() => {
    // ,   Telegram WebApp
    const isTelegramAvailable = Boolean(viewport && viewport.mount && typeof viewport.mount === 'function');

    if (isTelegramAvailable) {
      //  viewport
      try {
        viewport.mount();

        //  CSS      viewport
        if (viewport.bindCssVars && typeof viewport.bindCssVars === 'function') {
          viewport.bindCssVars();
        }

        //    
        if (viewport.expand && typeof viewport.expand === 'function') {
          viewport.expand();
        }

        //       
        if (viewport.requestFullscreen && typeof viewport.requestFullscreen === 'function') {
          viewport.requestFullscreen()
            .then(() => {
              setIsFullscreen(true);
            })
            .catch(err => {
              console.error('    :', err);
            });
        }
      } catch (error) {
        console.error('   Telegram WebApp:', error);
      }
    }

    //    
    return () => {
      if (viewport && viewport.unmount && typeof viewport.unmount === 'function') {
        try {
          viewport.unmount();
        } catch (error) {
          console.error('   Telegram WebApp:', error);
        }
      }
    };
  }, []);

  //     
  const toggleFullscreen = async () => {
    try {
      if (isFullscreen) {
        //    
        if (viewport.exitFullscreen && typeof viewport.exitFullscreen === 'function') {
          await viewport.exitFullscreen();
          setIsFullscreen(false);
        }
      } else {
        //    
        if (viewport.requestFullscreen && typeof viewport.requestFullscreen === 'function') {
          await viewport.requestFullscreen();
          setIsFullscreen(true);
        }
      }
    } catch (error) {
      console.error('    :', error);
    }
  };

  // ,    Telegram WebApp
  const isTelegramAvailable = Boolean(viewport && viewport.requestFullscreen);

  return (
    <Router basename="/app">
      <div className="App">
        {/*      Telegram  */}
        {isTelegramAvailable && (
          <button 
            onClick={toggleFullscreen} 
            className="fullscreen-button"
          >
            {isFullscreen ? '  ' : ' '}
          </button>
        )}

        <nav>
          <Link to="/"></Link>
          <Link to="/sequencer"></Link>
          <Link to="/savedsessions"> </Link>
        </nav>

        <Routes>
          <Route path="/" element={
            <div className="cover-container">
              <img 
                src="cover.PNG" 
                alt="" 
                className="cover-image" 
              />
            </div>
          } />
          <Route path="/sequencer" element={<SequencerPage />} />
          <Route path="/savedsessions" element={<SavedSessionsPage />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;