import React, { useEffect, useState } from 'react';
import audioEngine from '../../AudioEngine';
import './PrerollOverlay.css';

const PrerollOverlay = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [isFadingOut, setIsFadingOut] = useState(false);
  const [totalBeats, setTotalBeats] = useState(0);
  const [currentBeat, setCurrentBeat] = useState(null);

  useEffect(() => {
    //   preroll
    const handlePrerollStart = (beatsCount) => {
      setTotalBeats(beatsCount);
      setCurrentBeat(null);
      setIsVisible(true);
    };

    //     preroll
    const handlePrerollBeat = (beatIndex, totalBeats) => {
      setCurrentBeat(beatIndex);
      
      //      
      if (beatIndex === totalBeats - 1) {
        setTimeout(() => {
          startFadeOut();
        }, 200);
      }
    };
    
    //   
    const handlePrerollCancel = () => {
      //       
      //  ,       
      startFadeOut();
    };

    //   
    audioEngine.setPrerollStartCallback(handlePrerollStart);
    audioEngine.setPrerollBeatCallback(handlePrerollBeat);
    audioEngine.setPrerollCancelCallback(handlePrerollCancel);

    //    
    return () => {
      audioEngine.setPrerollStartCallback(null);
      audioEngine.setPrerollBeatCallback(null);
      audioEngine.setPrerollCancelCallback(null);
    };
  }, []);

  //     
  const startFadeOut = () => {
    setIsFadingOut(true);
    setTimeout(() => {
      setIsVisible(false);
      setIsFadingOut(false);
    }, 300);
  };

  //     ( )
  const hideOverlay = () => {
    //     
    audioEngine.cancelPreroll();
    
    //       
    //    
    startFadeOut();
  };

  //   ,  overlay    
  if (!isVisible) return null;

  //     (  1  0)
  const displayNumber = currentBeat !== null ? currentBeat + 1 : "";

  return (
    <div 
      className={`preroll-overlay ${isFadingOut ? 'fading-out' : ''}`} 
      onClick={hideOverlay}
    >
      <div className="preroll-content">
        <div className={`preroll-counter ${currentBeat !== null ? 'animate' : ''}`}>
          {displayNumber}
        </div>
        <div className="preroll-progress">
          {Array.from({ length: totalBeats }).map((_, index) => (
            <div
              key={index}
              className={`progress-dot ${index === currentBeat ? 'active' : ''} ${
                index < currentBeat ? 'passed' : ''
              }`}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default PrerollOverlay; 