import React from 'react';
import './Sequencer.css';

export const BlocksStructure = ({ 
  sequencerStructure, 
  trackNames, 
  getCellState, 
  handleCellClick,
  currentPlaybackPosition = null,
  onLoopButtonClick,
  loopRange,
  loopedBlockIndices,
  mutedTracks,
  isGridModeActive,
  onTrackStepsChange
}) => (
  <div className="blocks-container">
    {sequencerStructure.map((block, blockIndex) => {
      //      (  )
      const originalIndex = block.originalIndex;
      const blockId = block.id;

      // ,       (  wrap-around)
      let isBlockInLoop = false;
      if (loopRange && loopRange.start !== null && loopRange.end !== null) {
        const S = loopRange.start;
        const E = loopRange.end;
        const N = sequencerStructure.length; 
        if (S <= E) {
          isBlockInLoop = (originalIndex >= S && originalIndex <= E);
        } else { // Wrap-around S > E
          isBlockInLoop = (originalIndex >= S && originalIndex < N) || 
                          (originalIndex >= 0 && originalIndex <= E);
        }
      }

      //  :      loopedBlockIndices
      const isBlockSelectedForLoop = loopedBlockIndices && loopedBlockIndices.includes(originalIndex);

      // ,      
      const isLoopActive = loopedBlockIndices && loopedBlockIndices.length > 0;
      const isOutOfLoop = isLoopActive && !isBlockSelectedForLoop;

      //       LOOP
      const loopButtonText = isBlockSelectedForLoop ? 'LOOPING' : 'LOOP';
      const loopButtonClass = `loop-button ${isBlockSelectedForLoop ? 'active' : ''}`; 

      return (
        <div 
          key={blockId || `block-${blockIndex}`} 
          className={`block-container ${isOutOfLoop ? 'block-out-of-loop' : ''}`}
        >
          <div className="sequencer">
            <div className="sequencer-header">
              <h2>{block.name}</h2>
              <button 
                className={loopButtonClass}
                onClick={() => onLoopButtonClick(originalIndex)}
              >
                {loopButtonText}
              </button>
            </div>

            <div className="grid">
              {trackNames.map(trackName => {
                const currentStepCount = block.trackSteps[trackName] || 0;
                return (
                  <div 
                    key={trackName} 
                    className={`track-row ${mutedTracks && mutedTracks.has(trackName) ? 'muted' : ''}`}
                  >
                    {isGridModeActive && (
                      <div className="track-step-controls">
                        <button 
                          onClick={() => {
                            if (blockId && currentStepCount > 1) {
                              onTrackStepsChange(blockId, trackName, currentStepCount - 1);
                            }
                          }}
                          disabled={!blockId || currentStepCount <= 1}
                          className="step-control-button"
                        >
                          -
                        </button>
                        <input 
                          type="number"
                          value={currentStepCount}
                          onChange={(e) => {
                            const newCount = parseInt(e.target.value, 10);
                            if (blockId && !isNaN(newCount) && newCount >= 1 && newCount <= 16) {
                              onTrackStepsChange(blockId, trackName, newCount);
                            }
                          }}
                          min="1"
                          max="16"
                          className="step-count-input"
                        />
                        <button 
                          onClick={() => {
                            if (blockId && currentStepCount < 16) {
                              onTrackStepsChange(blockId, trackName, currentStepCount + 1);
                            }
                          }}
                          disabled={!blockId || currentStepCount >= 16}
                          className="step-control-button"
                        >
                          +
                        </button>
                      </div>
                    )}
                    {Array.from({ length: currentStepCount }, (_, stepIndex) => {
                      const cellId = `${blockIndex}-${trackName}-${stepIndex}`;
                      const cellState = getCellState(cellId);
                      const isActive = !!cellState.instrumentId;
                      
                      const isCurrentlyPlaying = currentPlaybackPosition && 
                        currentPlaybackPosition.blockIndex === blockIndex && 
                        currentPlaybackPosition.stepIndex === stepIndex &&
                        (!mutedTracks || !mutedTracks.has(trackName));
                      
                      let playingClass = '';
                      if (isCurrentlyPlaying) {
                        playingClass = isActive ? 'playing-active' : 'playing-empty';
                      }

                      return (
                        <div
                          key={cellId}
                          className={`grid-cell ${isActive ? 'active' : ''} ${playingClass}`}
                          onClick={() => handleCellClick(cellId)}
                          data-cell-id={cellId}
                        >
                          {cellState.instrumentId && (
                            <img 
                              src={`./icons/${cellState.instrumentId}.png`}
                              alt={cellState.instrumentId}
                              className="cell-instrument-icon"
                              title={cellState.instrumentId}
                            />
                          )}
                        </div>
                      );
                    })}
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      );
    })}
  </div>
);