import React, { useState } from 'react';
import instruments from './instruments';
import './InstrumentPanel.css';

const InstrumentPanel = ({ 
  onInstrumentSelect, 
  onClearAll,
  //     GRID
  isGridModeActive,
  onToggleGridMode,
  currentBarInfo,
  onTimeSignatureChange,
  onAddBar,
  onRemoveBar
}) => {
  //    
  const [selectedInstrument, setSelectedInstrument] = useState(null);

  //   
  const handleInstrumentSelect = (instrumentId) => {
    if (selectedInstrument === instrumentId) {
      setSelectedInstrument(null);
      onInstrumentSelect?.(null, { id: null, name: "" });
    } else {
      setSelectedInstrument(instrumentId);
      if (onInstrumentSelect) {
        if (instrumentId === 'eraser') {
          onInstrumentSelect(instrumentId, { id: instrumentId, name: "" });
        } else {
          onInstrumentSelect(instrumentId, instruments[instrumentId]);
        }
      }
    }
  };

  //        +/-
  const handleTimeSignatureStepChange = (change) => {
    if (currentBarInfo && onTimeSignatureChange) {
      const currentSignature = currentBarInfo.timeSignature || 4;
      let newSignature = currentSignature + change;
      
      //      (,   - )
      const MIN_TS = 1;
      const MAX_TS = 6; 

      if (newSignature < MIN_TS) newSignature = MIN_TS;
      if (newSignature > MAX_TS) newSignature = MAX_TS;
      
      if (newSignature !== currentSignature) {
        onTimeSignatureChange(newSignature);
      }
    }
  };

  return (
    <div className="instrument-panel">
      <div className="instrument-list">
        {Object.keys(instruments).map((instrumentId) => (
          <div
            key={instrumentId}
            className={`instrument-item ${selectedInstrument === instrumentId ? 'selected' : ''}`}
            onClick={() => handleInstrumentSelect(instrumentId)}
            title={instruments[instrumentId].name}
          >
            <img src={instruments[instrumentId].icon} alt={instruments[instrumentId].name} />
          </div>
        ))}
        
        <div
          className={`instrument-item eraser ${selectedInstrument === 'eraser' ? 'selected' : ''}`}
          onClick={() => handleInstrumentSelect('eraser')}
          title=""
        >
          <img src="/icons/eraser.png" alt="" />
        </div>
      </div>
      
      <div className="panel-actions">
        {/*     ,   GRID */}
        {isGridModeActive && (
          <div className="grid-controls-panel">
            {currentBarInfo && (
              <div className="bar-info-display">
                <span>Time Signature: </span>
                <button
                  onClick={() => handleTimeSignatureStepChange(-1)}
                  disabled={(currentBarInfo.timeSignature || 4) <= 1}
                  className="time-signature-step-button"
                  title="  "
                >
                  -
                </button>
                <span className="time-signature-value">{currentBarInfo.timeSignature || 4}</span>
                <button
                  onClick={() => handleTimeSignatureStepChange(1)}
                  disabled={(currentBarInfo.timeSignature || 4) >= 6}
                  className="time-signature-step-button"
                  title="  "
                >
                  +
                </button>
                <span className="time-signature-denominator">/ 4</span>
              </div>
            )}

            <div className="bar-controls">
              <span className="bar-controls-label">Bars:</span>
              <button 
                onClick={onAddBar}
                className="grid-control-button add-bar-button"
                title="    "
              >
                +
              </button>
              <button 
                onClick={onRemoveBar}
                className="grid-control-button remove-bar-button"
                title="  "
              >
                -
              </button>
            </div>
          </div>
        )}

        <button
          className={`grid-button ${isGridModeActive ? 'active' : ''}`}
          onClick={onToggleGridMode}
          title="  "
        >
          GRID
        </button>
        <button 
          className="clear-button"
          onClick={onClearAll}
          title="  "
        >
          <span className="clear-icon"></span>
        </button>
      </div>
    </div>
  );
};

export default InstrumentPanel;