import React from 'react';
import './PlayerMenu.css';

const MIN_BPM = 60;
const MAX_BPM = 240;
const DEFAULT_BPM = 120;

const PlayerMenu = ({ 
  sequencerStructure, 
  trackNames, 
  cellsState, 
  currentBar = 0, 
  totalBars = 1, 
  onBarChange = () => {},
  currentBarInfo = {},
  onVerticalModeChange = () => {},
  inVerticalMode = false, //     
  
  //      
  isPlaying = false,
  bpm = DEFAULT_BPM,
  isInitialized = false, 
  isPrerollEnabled = true,
  isTransitioning = false,
  onPlayToggle = () => {},
  onBpmChange = () => {},
  onPrerollToggle = () => {},
  //   
  isMetronomeEnabled = false,
  onMetronomeToggle = () => {}
}) => {
  //  timeSignature  
  const timeSignature = currentBarInfo?.timeSignature || 4;
  
  //   BPM
  const handleBpmChange = (e) => {
    const newBpm = parseInt(e.target.value, 10);
    if (!isNaN(newBpm) && newBpm >= MIN_BPM && newBpm <= MAX_BPM) {
      onBpmChange(newBpm);
    }
  };

  //    
  const goToPreviousBar = () => {
    if (currentBar > 0) {
      onBarChange(currentBar - 1);
    }
  };

  const goToNextBar = () => {
    if (currentBar < totalBars - 1) {
      onBarChange(currentBar + 1);
    }
  };
  
  //    preroll
  const togglePreroll = () => {
    onPrerollToggle(!isPrerollEnabled);
  };

  //        
  const menuClassName = `player-menu ${inVerticalMode ? 'in-vertical-mode' : ''}`;

  return (
    <div className={menuClassName}>
      <div className="transport-controls">
        <button 
          className={`play-button ${isPlaying ? 'active' : ''}`} 
          onClick={onPlayToggle}
          disabled={!isInitialized || isTransitioning}
        >
          {isPlaying ? '' : ''}
        </button>
        
        {/*         */}
        {!inVerticalMode && (
          <>
            <button 
              className={`display-mode-button`} 
              onClick={() => onVerticalModeChange('vertical')}
            >
               
            </button>
            <button
              className={`preroll-button ${isPrerollEnabled ? 'active' : ''}`}
              onClick={togglePreroll}
            >
              
            </button>
            {/*   */} 
            <button
              className={`metronome-button ${isMetronomeEnabled ? 'active' : ''}`}
              onClick={onMetronomeToggle}
            >
              
            </button>
          </>
        )}
      </div>
      
      <div className="bpm-controls">
        <label htmlFor="bpm-input">BPM:</label>
        <input
          id="bpm-input"
          type="number"
          min={MIN_BPM}
          max={MAX_BPM}
          value={bpm}
          onChange={handleBpmChange}
        />
        
        <input
          type="range"
          min={MIN_BPM}
          max={MAX_BPM}
          value={bpm}
          onChange={handleBpmChange}
          className="bpm-slider"
        />
      </div>

      <div className="bar-controls">
        <button 
          onClick={goToPreviousBar}
          disabled={currentBar === 0}
          className="bar-button"
        >
          &lt;
        </button>
        <span className="bar-indicator">
          {currentBar + 1} / {totalBars}
        </span>
        <button 
          onClick={goToNextBar}
          disabled={currentBar === totalBars - 1}
          className="bar-button"
        >
          &gt;
        </button>
      </div>
    </div>
  );
};

export default PlayerMenu; 