import React from 'react';

const useEditCells = (initialCellsState, setCellsState) => {
  //    
  const handleCellClick = (cellId, selectedInstrument) => {
    const selectedInstrumentId = selectedInstrument?.id;

    if (selectedInstrumentId === null || selectedInstrumentId === undefined) {
      console.log("  ,    ");
      return;
    }

    setCellsState(prevState => {
      const cell = prevState[cellId] || { instrumentId: null };

      if (selectedInstrumentId === 'eraser') {
        if (cell.instrumentId) {
          return {
            ...prevState,
            [cellId]: {
              ...cell,
              instrumentId: null
            }
          };
        }
        return prevState;
      }

      let newInstrumentId;

      if (!cell.instrumentId) {
        newInstrumentId = selectedInstrumentId;
      } 
      else if (cell.instrumentId === selectedInstrumentId) {
        newInstrumentId = null;
      } 
      else {
        newInstrumentId = selectedInstrumentId;
      }

      return {
        ...prevState,
        [cellId]: {
          ...cell,
          instrumentId: newInstrumentId
        }
      };
    });

    console.log(`Clicked cell: ${cellId}, instrument: ${selectedInstrumentId || 'none'}`);
  };

  //    
  const clearAllCells = () => {
    const clearedState = {};
    Object.keys(initialCellsState).forEach(cellId => {
      clearedState[cellId] = {
        ...initialCellsState[cellId],
        instrumentId: null
      };
    });
    setCellsState(clearedState);
  };

  return {
    handleCellClick,
    clearAllCells
  };
};

export default useEditCells; 