import React, { useRef } from 'react';
import { useLocation } from 'react-router-dom';
import SequencerModule from '../modules/sequencer/SequencerModule';

const SequencerPage = () => {
  const sequencerModuleRef = useRef(null);
  const location = useLocation();

  const initialSessionData = location.state?.loadedSessionData || null;

  const handleExportClick = () => {
    const sessionName = window.prompt("    :");
    if (sessionName && sessionName.trim() !== "") {
      let folderNameInput = window.prompt("   (        ):");
      //    "" (folderNameInput === null)    , 
      //  folderName   ,      null.
      const folderName = folderNameInput ? folderNameInput.trim() : ""; 

      if (sequencerModuleRef.current) {
        sequencerModuleRef.current.exportData(sessionName.trim(), folderName);
      }
    } else if (sessionName !== null) { //  ,   
      alert("     .");
    }
    //  sessionName === null (    ),   
  };

  const buttonStyle = {
    backgroundColor: '#4CAF50', /* Green */
    border: 'none',
    color: 'white',
    padding: '10px 20px',
    textAlign: 'center',
    textDecoration: 'none',
    display: 'inline-block',
    fontSize: '16px',
    margin: '4px 2px',
    marginBottom: '10px',
    cursor: 'pointer',
    borderRadius: '8px',
    boxShadow: '0 2px 4px 0 rgba(0,0,0,0.2)',
    transitionDuration: '0.4s',
  };

  return (
    <div className="sequencer-page">
      <h1> {initialSessionData ? `(${initialSessionData.sessionName})` : ''}</h1>
      <button onClick={handleExportClick} style={buttonStyle}>
          
      </button>
      <SequencerModule ref={sequencerModuleRef} initialSessionData={initialSessionData} />
    </div>
  );
};

export default SequencerPage; 